import React, { useEffect } from "react";
import "./Splashscreen.css";

function Splashscreen({ onFinish }) {
  useEffect(() => {
    const timer = setTimeout(() => {
      onFinish(); // notify parent that splash ended
    }, 2500); // 2.5s for animation + fade

    return () => clearTimeout(timer);
  }, [onFinish]);
  return (
    <div className="splash-container">
      <div className="big-b">B</div>
      <div className="text" id="fadeText">
        <span>B</span>
        <span>A</span>
        <span>S</span>
        <span>K</span>
        <span>E</span>
        <span>T</span>
        <span>B</span>
        <span>A</span>
        <span>Y</span>
      </div>
    </div>
  );
}

export default Splashscreen;
