import axios from "axios";
import React, { useContext, useState, useEffect } from "react";
import { Modal, Button, Form } from "react-bootstrap";
import Context from "../Context/Context";
import "./CustomerProfile.css";

function CustomerProfile({ show, handleClose }) {
  const { formValues, setFormValues, setToast } = useContext(Context);
  const [editMode, setEditMode] = useState(false);
  const [editedValues, setEditedValues] = useState({});

  useEffect(() => {
    setEditedValues(formValues);
  }, [formValues]);

  if (!formValues.isLoggedIn) return null;

  console.log(`formValues: ${formValues}`);

  // Handle edit toggle
  const handleEditToggle = () => {
    setEditMode(!editMode);
  };

  // Handle input change
  const handleChange = (e) => {
    const { name, value } = e.target;
    setEditedValues((prev) => ({ ...prev, [name]: value }));
  };

  // Check if user actually changed any details
  const hasChanges = () => {
    return Object.keys(editedValues).some(
      (key) => editedValues[key] !== formValues[key]
    );
  };

  const handleSave = async () => {
    if (!hasChanges()) {
      alert("No changes detected!");
      setEditMode(false);
      return;
    }

    try {
      const res = await axios.patch(
        `${process.env.REACT_APP_SERVER_URL}/api/auth/update-profile`,
        editedValues
      );

      if (res.status === 200) {
        setFormValues((prev) => ({ ...prev, ...editedValues }));
        setToast({
          show: true,
          message: "Profile updated successfully!",
          type: "error",
        });
        setEditMode(false);
      }
    } catch (err) {
      console.error("Profile update failed:", err);
      setToast({
        show: true,
        message: "Error while updating the profile!",
        type: "error",
      });
    }
  };

  return (
    <Modal show={show} onHide={handleClose} centered size="md">
      <Modal.Header closeButton>
        <Modal.Title>Profile</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <div className="container-fluid">
          {/* Static Image */}
          <div className="row mb-3">
            <div className="col-sm-12 col-md-12 col-lg-12 col-xl-12 text-center">
              <img
                alt=""
                src="https://res.cloudinary.com/dfgdhixnn/image/upload/v1761208233/profile_dfv25t.png"
                width={100}
                height={100}
              />
            </div>
          </div>

          {/* Edit Switch */}
          <div className="row mb-3">
            <div className="d-flex justify-content-center align-items-center gap-2">
              <div className="form-check form-switch">
                <input
                  className="form-check-input"
                  type="checkbox"
                  id="editSwitch"
                  checked={editMode}
                  onChange={handleEditToggle}
                />
                <label className="form-check-label ms-2" htmlFor="editSwitch">
                  Edit Profile
                </label>
              </div>
            </div>
          </div>

          {/* Profile */}

          <div className="row">
            <div className="col-sm-12 col-md-12 col-lg-12 col-xl-12">
              <Form>
                {/* First row: Firstname & Lastname */}
                <div className="row mb-3">
                  <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6 mb-2">
                    <Form.Floating>
                      <Form.Control
                        name="firstname"
                        type="text"
                        placeholder="First Name"
                        className="profile-input"
                        value={editedValues.firstname ?? ""}
                        readOnly={!editMode}
                        onChange={handleChange}
                      />
                      <label>First Name</label>
                    </Form.Floating>
                  </div>
                  <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6 mb-2">
                    <Form.Floating>
                      <Form.Control
                        name="lastname"
                        type="text"
                        placeholder="Last Name"
                        className="profile-input"
                        value={editedValues.lastname ?? ""}
                        onChange={handleChange}
                        readOnly={!editMode}
                      />
                      <label>Last Name</label>
                    </Form.Floating>
                  </div>
                </div>

                {/* Second row: Email & Age */}
                <div className="row mb-3">
                  <div className="col-sm-12 col-md-12 col-lg-12 col-xl-12 mb-2">
                    <Form.Floating>
                      <Form.Control
                        name="email"
                        type="email"
                        placeholder="Email"
                        value={editedValues.email ?? ""}
                        readOnly
                        className="profile-input"
                      />
                      <label>Email</label>
                    </Form.Floating>
                  </div>
                </div>

                {/* Third row: Mobile & Gender */}
                <div className="row mb-3">
                  <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6 mb-2">
                    <Form.Floating>
                      <Form.Control
                        name="mobileno"
                        type="number"
                        placeholder="XXXXXXXXXX"
                        className="profile-input"
                        value={editedValues.mobileno ?? ""}
                        onChange={handleChange}
                        readOnly={!editMode}
                      />
                      <label>Mobile</label>
                    </Form.Floating>
                  </div>
                  <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6 mb-2">
                    <Form.Floating>
                      <Form.Select
                        name="gender"
                        id="floatingSelectGender"
                        aria-label="Select Gender"
                        value={editedValues.gender ?? ""}
                        onChange={handleChange}
                        disabled={!editMode}
                        className="profile-input"
                      >
                        <option value="male">MALE</option>
                        <option value="female">FEMALE</option>
                        <option value="others">OTHERS</option>
                      </Form.Select>
                      <label htmlFor="floatingSelectGender">Gender</label>
                    </Form.Floating>
                  </div>
                </div>

                {/* Fourth row: Address */}
                <div className="row mb-3">
                  <div className="col-sm-12 col-md-12 col-lg-12 col-xl-12 mb-2">
                    <Form.Floating>
                      <Form.Control
                        name="address"
                        type="text"
                        placeholder="Address"
                        className="profile-input"
                        value={editedValues.address ?? ""}
                        onChange={handleChange}
                        readOnly={!editMode}
                      />
                      <label>Address</label>
                    </Form.Floating>
                  </div>
                </div>

                {/* Fifth row: Pincode */}
                <div className="row mb-3">
                  <div className="col-sm-12 col-md-12 col-lg-12 col-xl-12 mb-2">
                    <Form.Floating>
                      <Form.Control
                        name="pincode"
                        type="text"
                        placeholder="Pincode"
                        value={editedValues.pincode ?? ""}
                        onChange={handleChange}
                        readOnly={!editMode}
                      />
                      <label>Pincode</label>
                    </Form.Floating>
                  </div>
                </div>
              </Form>
            </div>
          </div>
        </div>
      </Modal.Body>
      <Modal.Footer>
        {editMode && (
          <Button variant="success" onClick={handleSave}>
            Save
          </Button>
        )}
        <Button variant="outline-dark" size="md" onClick={handleClose}>
          Close
        </Button>
      </Modal.Footer>
    </Modal>
  );
}

export default CustomerProfile;
