import React, { useContext, useState } from "react";
import "./Orders.css";
import axios from "axios";
import Context from "../Context/Context";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTrashCan } from "@fortawesome/free-solid-svg-icons";
import { getProductImage } from "../utils/getProductImage";
import ToastMessage from "../ToastMessage/ToastMessage";
import Payment from "../Payment/Payment";

function Orders() {
  const {
    recentOrders,
    orders,
    setOrders,
    products,
    setProducts,
    formValues,
    toast,
    setToast,
    setPaymentModalVisible,
    setView,
  } = useContext(Context);

  const [paymentSessionId, setPaymentSessionId] = useState(null);
  const [verifying, setVerifying] = useState(false);

  const incrementCount = (id) => {
    setOrders((prev) =>
      prev.map((item) =>
        item.id === id ? { ...item, count: item.count + 1 } : item
      )
    );
  };

  const decrementCount = (id) => {
    setOrders((prev) =>
      prev
        .map((item) =>
          item.id === id && item.count > 1
            ? { ...item, count: item.count - 1 }
            : item
        )
        .filter((item) => item.count > 0)
    );
  };

  const removeItem = (id) => {
    setOrders((prevOrders) => {
      const itemToRemove = prevOrders.find((item) => item.id === id);
      if (itemToRemove) {
        setProducts((prevProducts) =>
          prevProducts.map((p) =>
            p.id === id ? { ...p, count: p.count + itemToRemove.count } : p
          )
        );
      }
      return prevOrders.filter((item) => item.id !== id);
    });
  };

  const totalPayable = orders.reduce(
    (sum, item) =>
      sum +
      (item.price - (item.price * (item.discount || 0)) / 100) * item.count,
    0
  );

  const totalAmount = Math.round(totalPayable);
  const customerId = formValues?._id;

  const handleCheckout = async () => {
    if (orders.length === 0) return;

    //Check if any product in cart is already pending in recentOrders
    const pendingProducts = recentOrders
      .filter((o) => o.status === "PENDING")
      .flatMap((o) => o.products.map((p) => p.productId));

    const duplicate = orders.find((item) => pendingProducts.includes(item.id));

    if (duplicate) {
      setToast({
        show: true,
        message: `Payment is still pending for ${duplicate.name}`,
        type: "error",
      });
      return; // Prevent checkout
    }

    setPaymentModalVisible(true); // show Payment modal
  };

  const handlePaymentSuccess = (method) => {
    setOrders([]);
    setPaymentSessionId(null); // hide verify button
    setToast({
      show: true,
      message: `${method} payment successful!`,
      type: "success",
    });
    setTimeout(() => {
      setView("RecentOrder");
    }, 1000);
  };

  const handlePaymentCancel = () => {
    setToast({
      show: true,
      message: "Payment cancelled",
      type: "error",
    });
  };

  //Verify Online Payment
  const handleVerifyPayment = async () => {
    if (!paymentSessionId || verifying) return;
    setVerifying(true);

    try {
      const res = await axios.post(
        `${process.env.REACT_APP_SERVER_URL}/api/payment/verify`,
        {
          sessionId: paymentSessionId,
        }
      );

      if (res.data.success) {
        handlePaymentSuccess("Online");
        setPaymentSessionId(null);
      } else {
        setToast({
          show: true,
          message: "Payment not completed or failed",
          type: "error",
        });
      }
    } catch (err) {
      console.error("Payment verification error:", err);
      setToast({
        show: true,
        message: "Error verifying payment",
        type: "error",
      });
    } finally {
      setVerifying(false);
    }
  };

  return (
    <>
      <div className="row">
        <h1 className="text-center myorders-title">
          Cart {orders.length ? `(${orders.length})` : "(0)"}
        </h1>
      </div>

      {orders.map((item) => {
        const product = products.find((p) => p._id === item.id);
        return (
          <div className="row orders-single" key={item.id}>
            <div className="offset-sm-1 offset-md-1 offset-lg-1 offset-xl-1 col-sm-10 col-md-2 col-lg-2 col-xl-2 order-image my-4 text-center">
              <img src={getProductImage(item.image)} alt={item.name} />
            </div>
            <div className="col-sm-10 col-md-6 col-lg-6 col-xl-6 text-center">
              <b>{item.name}</b>
              <p className="order-description">{item.description}</p>
              <div className="price-details">
                MRP: ₹{item.price}, Discount: {item.discount || 0}%, Final: ₹
                {Math.round(
                  item.price - (item.price * (item.discount || 0)) / 100
                )}
              </div>
            </div>
            <div className="col-sm-10 col-md-1 col-lg-1 col-xl-1 order-count">
              <button
                className="decrement"
                onClick={() => decrementCount(item.id)}
              >
                -
              </button>
              <span>{item.count}</span>
              <button
                className="increment"
                onClick={() => incrementCount(item.id)}
                disabled={!product || item.count >= product.initialStock}
              >
                +
              </button>
            </div>
            <div
              className="col-sm-10 col-md-1 col-lg-1 col-xl-1 delete"
              onClick={() => removeItem(item.id)}
            >
              <FontAwesomeIcon icon={faTrashCan} />
            </div>
          </div>
        );
      })}

      <div className="row total-row mt-2">
        <div className="col-sm-6  col-md-6 col-lg-6 col-xl-6 text-center total-label">
          Total
        </div>
        <div className="col-sm-6 col-md-6 col-lg-6 col-xl-6 text-center payable-amount">
          ₹{Math.round(totalPayable)}
        </div>
      </div>

      <div className="row mt-2">
        <div className="text-center">
          <button
            className={`PayNow ${orders.length === 0 ? "disabled" : ""}`}
            onClick={handleCheckout}
            disabled={orders.length === 0}
          >
            Check Out
          </button>
        </div>
      </div>

      {paymentSessionId && (
        <div className="row mt-2 text-center">
          <button className="VerifyPayment" onClick={handleVerifyPayment}>
            Verify Online Payment
          </button>
        </div>
      )}

      <Payment
        amount={totalAmount}
        customerId={customerId}
        onPaymentSuccess={(method) => {
          handlePaymentSuccess(method);
          setPaymentSessionId(null);
        }}
        onPaymentCancel={handlePaymentCancel}
        setPaymentSessionId={setPaymentSessionId} // pass setter to Payment
      />

      {toast.show && (
        <ToastMessage
          message={toast.message}
          type={toast.type}
          show={toast.show}
          onClose={() => setToast({ ...toast, show: false })}
        />
      )}
    </>
  );
}

export default Orders;
