import React, { useContext, useState } from "react";
import "./Payment.css";
import Context from "../Context/Context";
import Error from "../Error/Error";
import axios from "axios";

function Payment({
  amount,
  onPaymentSuccess,
  onPaymentCancel,
  customerId,
  setPaymentSessionId,
}) {
  const {
    setPaymentModalVisible,
    isPaymentModalVisible,
    StaticImages,
    orders,
    createPendingOrder,
    setRecentOrders,
  } = useContext(Context);

  const [showError, setShowError] = useState(false); // show error modal
  const [loading, setLoading] = useState(false); // disable buttons while processing

  const handleCloseModal = () => {
    setPaymentModalVisible(false);
    setShowError(false); // reset error when closing
    if (onPaymentCancel) onPaymentCancel();
  };

  const handleCOD = async () => {
    try {
      // Map frontend cart to backend schema
      const payloadProducts = orders.map((o) => ({
        productId: o.id, // map frontend 'id' to backend 'productId'
        name: o.name,
        price: o.price,
        quantity: o.count, // map 'count' to 'quantity'
        discount: o.discount || 0,
        imageUrl: o.image,
      }));

      const res = await axios.post(
        `${process.env.REACT_APP_SERVER_URL}/api/orders`,
        {
          customerId,
          products: payloadProducts,
          totalAmount: amount,
          paymentMethod: "COD",
          paymentDetails: {
            cashfree_order_id: "",
            payment_link: "",
            payment_status: "PENDING",
            payment_reference_id: "",
          },
        }
      );

      if (res.data.success) {
        handleCloseModal();
        setRecentOrders((prev) => [res.data.order, ...prev]);
        onPaymentSuccess("COD");
      } else {
        alert("Error placing COD order");
      }
    } catch (err) {
      console.error(
        "Server error while placing order:",
        err.response?.data || err
      );
      alert("Server error while placing order");
    }
  };

  const handleCashfree = async () => {
    setLoading(true);
    try {
      const paymentLink = await createPendingOrder(orders, amount, customerId);
      if (paymentLink && paymentLink.payment_session_id) {
        // Set session ID in Orders to enable verification
        if (setPaymentSessionId) setPaymentSessionId(paymentLink.orderId);

        // Redirect user to Cashfree payment page
        window.location.href = paymentLink.paymentLink;
      } else {
        setShowError(true);
      }
    } catch (err) {
      console.error("Payment link error:", err);
      setShowError(true);
    } finally {
      setLoading(false);
    }
  };

  if (!isPaymentModalVisible) return null;

  return (
    <>
      <div className="modal-overlay">
        <div className="modal-content">
          <button className="modal-close" onClick={() => handleCloseModal}>
            &times;
          </button>

          {showError ? (
            <div className="error-content">
              <Error message="Failed to initiate payment. Please try again." />
            </div>
          ) : (
            <>
              <h2>Payment Options</h2>
              <p>
                Total: ₹<b>{amount}</b>
              </p>
              <button onClick={handleCOD} className="payment-option-button">
                <img src={StaticImages.COD_ICON} alt="cash on delivery" /> Cash
                on Delivery
              </button>
              <button
                onClick={handleCashfree}
                className="payment-option-button"
              >
                <img src={StaticImages.CASHFREE_ICON} alt="online payment" />
                {loading ? "Processing..." : "Digital Payment"}
              </button>
            </>
          )}
        </div>
      </div>
    </>
  );
}

export default Payment;
