import "./Error.css";

function Error() {
  return (
    <div className="error-container">
      <div className="error-text">
        <h1>Under Maintenance</h1>
        <p>
          We’re currently fixing this issue. Please try again after sometime or
          choose a different payment option.
        </p>
      </div>
      <div className="error-image">
        <img
          src="https://res.cloudinary.com/dfgdhixnn/image/upload/v1761120550/maintenance2_zdbfdt.png"
          alt="Error illustration"
        />
      </div>
    </div>
  );
}

export default Error;
